import React from "react";
import "./MediumSizedComponent.css";


export default function MediumSizedComponent(props) {
    return (
        <div id="MediumSizedComponent" style={{ color: props.color, background: props.background }}>
            <div id="meduimSizedContentBody">
                <h2 id="mediumSizedHeading">{props.heading}</h2>
                <div id="mediumSizedBody">{props.body}</div>
                <div id="arrowContainer">
                    <span id="bottomTextMediumSizedComponenet">{props.bottomText}</span>
                    <i className="fas fa-arrow-right fa-2x"></i>
                </div>
            </div>
        </div>

    )
}