import React from "react";
import "./SmallSizedComponent.css";

export default function SmallSizedComponent(props) {

    const styling = {
        position: "absolute",
        left: "40px",
        top: props.date.length > 0 ? "330px" : "360px"
    }
    const headingStyling = {
        fontWeight: "bold",
        fontSize: "18px",
        lineHeight: "25px",
        marginTop: props.date.length > 0 ? "8px" : "0"
    }
    const smallComp = {
        backgroundRepeat: "no-repeat",
        backgroundSize: "100% 100%",
        backgroundImage: "linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(" + props.background + ")"
    }
    return (
        <div style={smallComp}
            className="smallImageComp"
            id={props.id}>
            <div id="firstSmallComp" style={styling}>
                <div id="smallComponenetDate">{props.date}</div>
                <h3 id="smallComponentHeading" style={headingStyling}>{props.heading}</h3>
                <div id="smallComponentAuthor">{props.author}</div>
            </div>
            <img src="./assets/horizontalLine.svg" id="crossLine" />
            <div id="secondSmallComp">
                <span id="readStory">READ STORY</span>
                <i className="fas fa-arrow-right fa-2x"></i>
            </div>
        </div>
    )
}