import React from "react";
import Navigation from "./Navigation";
import LargerImage from "./LargerImage";
import MediumSizedComponent from "./MediumSizedComponent";
import SmallSizedComponent from "./SmallSizedComponent";
import HomePageFeatures from "./HomePageFeatures";
import Footer from "./Footer";
import "./HomePage.css";

export default function HomePage() {
    return (
        <>
            <div className="container-fluid" id="homePageBody">
                <div id="homePageTopBody">
                    <div className="homePageTopBodycontentBox" id="content1">
                        <MediumSizedComponent
                            heading="Create and share your photo stories."
                            body="Photosnap is a platform for photographers and visual storytellers. We make it easy to share photos, tell stories and connect with others."
                            bottomText="GET AN INVITE"
                            background="black"
                            color="white"
                        />
                        <LargerImage
                            desktopSrc="./assets/home/desktop/create-and-share.jpg"
                            mobileSrc="./assets/home/mobile/create-and-share.jpg"
                            tabletSrc="./assets/home/tablet/create-and-share.jpg"
                            id="createNshare"
                        />
                    </div>
                    <div className="homePageTopBodycontentBox" id="content2">
                        <LargerImage
                            desktopSrc="./assets/home/desktop/beautiful-stories.jpg"
                            mobileSrc="./assets/home/mobile/beautiful-stories.jpg"
                            tabletSrc="./assets/home/tablet/beautiful-stories.jpg"
                            id="beautifulStories"
                        />
                        <MediumSizedComponent
                            heading="Beautiful stories every time."
                            body=" We provide design templates to ensure your stories look terrific. Easily add 
                        photos, text, embed maps and media from other networks. Then share your story 
                        with everyone."
                            bottomText="View the stories"
                            background="white"
                            color="black"
                        />
                    </div>
                    <div className="homePageTopBodycontentBox" id="content3" >
                        <MediumSizedComponent
                            heading="DESIGNED FOR EVERYONE."
                            body=" Photosnap can help you create stories that resonate with your audience.  Our tool is designed for photographers of all levels, brands, businesses you name it."
                            bottomText="View the stories"
                            background="white"
                            color="black"
                        />
                        <LargerImage
                            desktopSrc="./assets/home/desktop/designed-for-everyone.jpg"
                            mobileSrc="./assets/home/mobile/designed-for-everyone.jpg"
                            tabletSrc="./assets/home/desktop/designed-for-everyone.jpg"
                            id="designed4Everyone"

                        />
                    </div>
                </div>
                <div id="homePageMiddleBody">
                    <SmallSizedComponent
                        date=""
                        heading="The Mountains"
                        author="by John Appleseed"
                        background="./assets/stories/desktop/mountains.jpg"
                        id="one"
                    />
                    <SmallSizedComponent
                        date=""
                        heading="Sunset Cityscapes"
                        author="by Benjamin Cruz"
                        background="./assets/stories/desktop/cityscapes.jpg"
                        id="two"
                    />
                    <SmallSizedComponent
                        date=""
                        heading="18 Days Voyage"
                        author="by Alexei Borodin"
                        background="./assets/stories/desktop/18-days-voyage.jpg"
                        id="three"
                    />
                    <SmallSizedComponent
                        date=""
                        heading="Architecturals"
                        author="by Samantha Brooke"
                        background="./assets/stories/desktop/architecturals.jpg"
                        id="four"
                    />

                </div>
                <div className="container" id="homePageFeatures">
                    <HomePageFeatures
                        src="./assets/features/desktop/responsive.svg"
                        title="100% Responsive"
                        text="No matter which the device you’re on, our site is fully responsive and stories look beautiful on any screen."

                    />
                    <HomePageFeatures
                        src="./assets/features/desktop/no-limit.svg"
                        title="No Photo Upload Limit"
                        text="Our tool has no limits on uploads or bandwidth. Freely upload in bulk and share all of your stories in one go."

                    />
                    <HomePageFeatures
                        src="./assets/features/desktop/embed.svg"
                        title="Available to Embed"
                        text="Embed Tweets, Facebook posts, Instagram media, Vimeo or YouTube videos, Google Maps, and more."
                    />
                </div>
            </div>
        </>
    )
}