import React, { useState } from "react";
import "./Navigation.css";
import { Link } from "react-router-dom";
export default function Navigation(props) {
    const [isClicked, setIsClicked] = useState(false);
    function changeIcon() {
        setIsClicked(!isClicked);
    }
    return (
        <nav className="container navbar navbar-expand-sm">
            <Link to="/">
                <img src="./assets/logo.svg" className="navbar-brand" alt="logo brand" />
            </Link>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation" onClick={changeIcon}>
                {!isClicked ? <i className="fas fa-bars"></i> : <i className="fas fa-times"></i>}
            </button>
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                    <Link className="nav-item nav-link" to="/stories">Stories</Link>
                    <Link className="nav-item nav-link" to="/features">Features</Link>
                    <Link className="nav-item nav-link" to="/pricing">Pricing</Link>
                </div>
                <button id="inviteButton">GET AN INVITE</button>
            </div>

        </nav>
    )
}