import React from "react";
import { withRouter } from "react-router-dom";
import "./Features.css";
import HomePageFeatures from "./HomePageFeatures";

export default function Features(props) {
    const featuresInviteComp = {
        background: "url(./assets/shared/desktop/bg-beta.jpg)",
        height: "280px",
        color: "white",
        display: "flex",
        alignItems: "center",
    }
    return (
        <>
            <div id="featuresIntro">
                <img src="./assets/features/desktop/hero.jpg" />
                <span>
                    <div id="featuresComponent">
                        <h2>FEATURES</h2>
                        <div>We make sure all of our features are designed to be loved by every aspiring and even professional photograpers who wanted to share their stories</div>
                    </div>
                </span>
            </div>
            <div id="featuresBody" className="container">

                <HomePageFeatures
                    src="./assets/features/desktop/responsive.svg"
                    title="100% Responsive"
                    text="No matter which the device you’re on, our site is fully responsive and stories look beautiful on any screen."
                    id="one"
                />
                <HomePageFeatures
                    src="./assets/features/desktop/no-limit.svg"
                    title="No Photo Upload Limit"
                    text="No Our tool has no limits on uploads or bandwidth. Freely upload in bulk and share all of your stories in one go"
                    id="two"
                />
                <HomePageFeatures
                    src="./assets/features/desktop/embed.svg"
                    title="Available to Embed"
                    text="Embed Tweets, Facebook posts, Instagram media, Vimeo or YouTube videos, Google Maps, and more."
                    id="three"
                />
                <HomePageFeatures
                    src="./assets/features/desktop/custom-domain.svg"
                    title="Custom Domain"
                    text="With Photosnap subscriptions you can host your stories on your own domain. You can also remove our branding!"
                    id="four"
                />
                <HomePageFeatures
                    src="./assets/features/desktop/boost-exposure.svg"
                    title="Boost Your Exposure"
                    text="Users that viewed your story or gallery can easily get notifed of new and featured stories with our built in mailing list."
                    id="five"
                />
                <HomePageFeatures
                    src="./assets/features/desktop/drag-drop.svg"
                    title="Drag & Drop Image"
                    text="Easily drag and drop your image and get beautiful shots everytime. No over the top tooling to add friction to creating stories."
                    id="six"
                />

            </div>
            <div id="featuresInviteComp" style={featuresInviteComp}>
                <div className="container">
                    <h2>We’re in beta. Get your invite today!</h2>
                    <div style={{ display: "flex", alignItems: "center" }}>
                        <span>GET AN INVITE</span>
                        <i className="fas fa-arrow-right fa-2x"></i>
                    </div>
                </div>
            </div>
        </>
    )
}