import React from "react";
import "./Card.css";
export default function Card(props) {
    let amount = props.amount;
    let date = props.date;
    if (props.switchIsClicked) {
        amount += "0.00";
        date = "per year"
    } else {
        amount += ".00";
    }
    return (
        <div id={props.heading}>
            <h2 className="cardHeading">{props.heading}</h2>
            <div className="cardText">{props.text}</div>
            <h2 className="cardAmount">{amount}</h2>
            <div className="cardDate">{date}</div>
            <button className="button">pick plan</button>
        </div>
    )
}