import React from "react";
import "./HomePageFeatures.css";

export default function HomePageFeatures(props) {
    return (
        <div className="homePageFeaturesComp" id={props.id}>
            <img src={props.src} />
            <h3 >{props.title}</h3>
            <div>{props.text}</div>
        </div>
    )
}