import React from "react";
import "./Footer.css";
import { Link } from "react-router-dom";

export default function Footer(props) {

    return (
        <div id="footerComponent">
            <div className="container">
                <div id="firstComponent">
                    <img src="./assets/whiteLogo.svg" id="logoImage" />
                    <div>
                        <img src="./assets/shared/desktop/facebook.svg" className="socialLinks" />
                        <img src="./assets/shared/desktop/youtube.svg" className="socialLinks" id="secondSocialLink" />
                        <img src="./assets/shared/desktop/twitter.svg" className="socialLinks" />
                        <img src="./assets/shared/desktop/pinterest.svg" className="socialLinks" id="fourthSocialLink" />
                        <img src="./assets/shared/desktop/instagram.svg" className="socialLinks" />
                    </div>
                </div>
                <div id="webPages">
                    <Link className="footerLinks" to="/" id="home">Home</Link>
                    <Link className="footerLinks" to="/stories">Stories</Link>
                    <Link className="footerLinks" to="/features" id="features">Features</Link>
                    <Link className="footerLinks" to="/pricing">Pricing</Link>
                </div>
                <div id="thirdComponent">
                    <div id="getInvite">
                        <span>Get an Invite</span>
                        <i className="fas fa-arrow-right fa-2x clickArrow"></i>
                    </div>
                    <div id="copyRight">
                        Copyright 2019. All Rights Reserved
                    </div>

                </div>

            </div>

        </div>
    )
}