import React, { useState } from "react";
import "./Pricing.css";
import { Switch, Grid } from '@material-ui/core';
import Card from "./Card";
import Table from "./Table"

export default function Pricing(props) {
    const [isChecked, setIsChecked] = useState(false);
    const pricingInviteComp = {
        background: "url(./assets/shared/desktop/bg-beta.jpg)",
        height: "280px",
        color: "white",
        display: "flex",
        alignItems: "center",
    }

    return (
        <>
            <div id="pricingIntro">
                <img src="./assets/pricing/desktop/hero.jpg" />
                <span>
                    <div id="pricingComponent">
                        <h2>pricing</h2>
                        <div>Create a your stories, Photosnap is a platform for photographers and visual storytellers. It’s the simple way to create and share your photos.</div>
                    </div>
                </span>

            </div>
            <div id="gridComponent">
                <Grid
                    component="label"
                    container
                    alignItems="center"
                    justifyContent="center"
                    spacing={1}
                >
                    <Grid item><div className="dateTimes">Monthly</div></Grid>
                    <Grid item>
                        <Switch
                            onChange={() => setIsChecked(!isChecked)}
                        />
                    </Grid>
                    <Grid item><div className="dateTimes">Yearly</div></Grid>
                </Grid>
            </div>
            <div id="pricingFeatures" className="container">
                <Card
                    heading="Basic"
                    text="Includes basic usage of our platform. Recommended for new and aspiring photographers."
                    amount="$19"
                    date="per month"
                    switchIsClicked={isChecked}
                />
                <Card
                    heading="Pro"
                    text="More advanced features available. Recommended for photography veterans and professionals."
                    amount="$39"
                    date="per month"
                    switchIsClicked={isChecked}
                />
                <Card
                    heading="Business"
                    text="Additional features available such as more detailed metrics. Recommended for business owners."
                    amount="$99"
                    date="per month"
                    switchIsClicked={isChecked}
                />
            </div>
            <div className="container" id="tableComponent">
                <div>compare</div>
                <Table />
            </div>
            <div id="pricingInviteComp" style={pricingInviteComp}>
                <div className="container">
                    <h2>We’re in beta. Get your invite today!</h2>
                    <div style={{ display: "flex", alignItems: "center" }}>
                        <span>GET AN INVITE</span>
                        <i className="fas fa-arrow-right fa-2x"></i>
                    </div>
                </div>
            </div>
        </>
    )
}